html {
  height:100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#Root{
  height:100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Navbar{
  background: rgb(99, 159, 170);
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  text-decoration: none;
  list-style-type: none;
  height:10%;
  margin-bottom: 30px;
}

.Navbar a{
  text-decoration: none;
  list-style-type: none;
  margin-left: 10px;
  color:white;
  outline: none;
}

.Navbar a:hover{
  text-decoration: underline;
  list-style-type: none;
  margin-left: 10px;
  color:red;
  outline: none;
}
.ResultContainer {
  height: 100%;
  /* overflow: auto; */
  max-width: 100%;
}

.Result{
  max-width:100%;
  height:100px;
  background: rgb(215, 240, 182);
  margin:10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
} 

.ResultTitle{
  font-size: 18px;
  font-weight: bold;
  padding-left:10px;
}

.ResultDescription{
  font-size: 14px;
  word-wrap: break-word;
  overflow-wrap: break-word;
  padding-left:10px;
}
* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}


#root{
  height: 100%;
  background: white;
}

.GitSearch{
  min-width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
  background:rgba(200, 201, 198, 0.61);
  align-items: center;
}

.SearchContainer{
  display: flex;
  width: 50%;
  justify-content: center;
}

.CustomizableSelect{
  height: 40px;
  margin:2px;
  width:20%;
}

.CustomizableSearch{
  height: 40px;
  margin:2px;
  width:60%;
  padding-left:10px;
}

.CustomizableButton{
  background-color:rgb(99, 159, 170);
  border:none;
  margin:2px;
  height: 40px;
  min-width: 10%;
  width:20%;
  flex-shrink: none;
  color:white;
}

.AvatarDescription{
  font-weight: bold;
}

img {
  width: 200px;
  height: 200px;
}

.ResultsContainer{
  margin-top: 20px;
  max-width: 50%;
}



@media screen and (max-width: 600px) {
  .SearchContainer {
    display: flex;
    align-items: center;
    flex-shrink: wrap;
    width: 100%;
  }
  .ResultsContainer{
    margin-top: 20px;
    max-width: 100%;
  }
}
